import { config } from '../../config';

const authorizationEndpoint = "https://accounts.google.com/o/oauth2/v2/auth";
const tokenEndpoint = "https://oauth2.googleapis.com/token";
const userInfoEndpoint = "https://www.googleapis.com/oauth2/v3/userinfo";

const clientId = config.googleClientId;
const clientSecret = config.googleClientSecret;
const redirectUri = config.googleRedirectUri;

console.log(process.env.REACT_APP_GOOGLE_CLIENT_ID);

const scope = "https://www.googleapis.com/auth/userinfo.profile";

const stakco_backend_url = "https://stakco.uc.r.appspot.com/api/stakco";
// const stakco_backend_url = "https://localhost:52729/api/stakco";

export function redirectToAuthorizationEndpoint() {
    const authorizationUrl = `${authorizationEndpoint}?response_type=code&client_id=${clientId}&redirect_uri=${redirectUri}&scope=${scope}`;
    window.location.href = authorizationUrl;
}

export async function handleAuthorizationCallback(authorizationCode) {
    const tokenRequestBody = new URLSearchParams({
        code: authorizationCode,
        client_id: clientId,
        client_secret: clientSecret,
        redirect_uri: redirectUri,
        grant_type: 'authorization_code',
    });

    try {
        const tokenResponse = await fetch(tokenEndpoint, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
            },
            body: tokenRequestBody,
        });

        if (!tokenResponse.ok) {
            throw new Error(`Token request failed with status ${tokenResponse.status}`);
        }

        const tokenData = await tokenResponse.json();

        const accessToken = tokenData.access_token;
        const refreshToken = tokenData.refresh_token;
        const expiresIn = tokenData.expires_in;

        localStorage.setItem('accessToken', accessToken);
        localStorage.setItem('refreshToken', refreshToken);
        localStorage.setItem('expiresIn', expiresIn);
        localStorage.setItem('isAuthenticated', true);

        await fetchUserDetails(accessToken);
        return true;
    } catch (error) {
        console.error("Error exchanging authorization code for access token:", error);
        return false;
    }
}

export async function refreshAccessToken(refreshToken) {
    const refreshRequestBody = new URLSearchParams({
        client_id: clientId,
        client_secret: clientSecret,
        refresh_token: refreshToken,
        grant_type: 'refresh_token',
    });

    try {
        const refreshResponse = await fetch(tokenEndpoint, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
            },
            body: refreshRequestBody,
        });

        if (!refreshResponse.ok) {
            throw new Error(`Refresh token request failed with status ${refreshResponse.status}`);
        }

        const refreshData = await refreshResponse.json();

        const accessToken = refreshData.access_token;
        const expiresIn = refreshData.expires_in;

        localStorage.setItem('accessToken', accessToken);
        localStorage.setItem('expiresIn', expiresIn);

        return true;
    } catch (error) {
        console.error("Error refreshing access token:", error);
        return false;
    }
}

export async function fetchUserDetails(accessToken) {
    try {
      const profileResponse = await fetch(userInfoEndpoint, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      });
  
      if (!profileResponse.ok) {
        throw new Error(`Profile request failed with status ${profileResponse.status}`);
      }
  
      const profileData = await profileResponse.json();
      localStorage.setItem('username', profileData.sub);
      localStorage.setItem('userfullname', profileData.name);
      localStorage.setItem('profilePicture', profileData.picture);
      const userId = profileData.sub;
        const permissionResponse = await fetch(`${stakco_backend_url}/userpermission/${userId}`, {
        method: 'GET',
        });

        if (!permissionResponse.ok) {
            throw new Error(`Permission request failed with status ${permissionResponse.status}`);
        }

        const permissionData = await permissionResponse.json();

        if(permissionData && permissionData.permissionLevel){
            localStorage.setItem('userpermission', permissionData.permissionLevel);
        } else {
            localStorage.setItem('userpermission', "Default");
        }    

    } catch (error) {
      console.error("Unable to fetch user details:", error);
    }
  }
  
