const stakco_unity_app_url = "https://stakcos.com/player"; //process.env.REACT_APP_STAKCO_UNITY_APP_URL;

export function getSimulationURI(puzzleId) {
    const username = localStorage.getItem('username');
    return `${stakco_unity_app_url}?username=${username}&puzzle=${puzzleId}`;
}

export function getPuzzleLayerURI(puzzleId, layerIndex) {
    // Assuming the URL pattern for layers is known and follows a specific structure.
    return `${stakco_unity_app_url}/puzzle/${puzzleId}/layer/${layerIndex}`;
}

export function getPuzzleLayerDownloadLink(puzzleId, layerIndex) {
    return getPuzzleLayerURI(puzzleId, layerIndex);
}

export function downloadAllStakcoLayers(stakcoLayers) {
    stakcoLayers.forEach((url, index) => {
        fetch(url, { method: 'GET', mode: 'cors' })
            .then((response) => response.blob())
            .then((blob) => {
                const a = document.createElement('a');
                a.href = URL.createObjectURL(blob);
                a.download = `stakco_layer_${index + 1}.png`;
                document.body.appendChild(a);
                a.click();
                document.body.removeChild(a);
            })
            .catch((error) => console.error('Error downloading image:', error));
    });
}
