import React, { useState, useCallback, useImperativeHandle, forwardRef } from "react";
import { Accordion, AccordionSummary, AccordionDetails, Grid, Box, Card, Button } from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import Cropper from "react-easy-crop";
import ImageButton from "./ImageButton";
import { getCroppedImg } from './../utils/cropImage';
import "./InputSelection.css";  // Add this line to import custom CSS

const InputSelection = forwardRef(({ image, name, onInputImageSelected, expanded }, ref) => {
  const [crop, setCrop] = useState({ x: 0, y: 0 });
  const [zoom, setZoom] = useState(1);
  const [croppedAreaPixels, setCroppedAreaPixels] = useState(null);
  // const [croppedImage, setCroppedImage] = useState(null);
  //const [inputImage, setInputImage] = useState(image);


  const handleCrop = useCallback(async () => {
    try {
      const croppedImgBlob = await getCroppedImg(image, croppedAreaPixels);
      // const croppedImgUrl = URL.createObjectURL(croppedImgBlob);
      // setCroppedImage(croppedImgUrl);
      onInputImageSelected(croppedImgBlob);
      setZoom(1);
      return croppedImgBlob;
    } catch (e) {
      console.error(e);
      return null;
    }
  }, [image, croppedAreaPixels, onInputImageSelected]);

  const handleReset = () => {
    //setInputImage(null);
    console.log("handle reset");
    image=null;
    setCrop({ x: 0, y: 0 });
    setZoom(1);
    setCroppedAreaPixels(null);
    // setCroppedImage(null);
    onInputImageSelected(null);
  };

  useImperativeHandle(ref, () => ({
    handleCrop
  }), [handleCrop]);

  const onCropComplete = useCallback((croppedArea, croppedAreaPixels) => {
    setCroppedAreaPixels(croppedAreaPixels);
  }, []);

  const handleFileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        onInputImageSelected(file);
        setZoom(1); // Reset zoom level when a new image is selected
      };
      reader.readAsDataURL(file);
    }
  };

  return (
    <Accordion expanded={expanded}>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls={`panel-input-content`}
        id={`panel-input-header`}
        sx={{
          flexDirection: "row-reverse",
          justifyContent: "space-between",
          marginBottom: 0,
          height: 70,
          backgroundColor: image ? 'lightgreen' : 'lightcoral',
        }}
      >
        <Grid container spacing={1}>
          <Grid item xs={12}>
            <Box
              sx={{
                display: "flex",
                alignItems: "center",
                height: "100%",
                justifyContent: "space-between",
              }}
            >
              <span>Stage 0 - Input</span>
              <span sx={{ backgroundColor: "red" }}>{name}</span>
              <span>
                <Button variant="outlined" color="secondary" onClick={handleReset} disabled={!image}>
                  Reset
                </Button>
              </span>
            </Box>
          </Grid>
          
        </Grid>
      </AccordionSummary>
      <AccordionDetails
        sx={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          height: "auto",
          marginTop: 1
        }}
      >
        <Card variant="outlined" sx={{ width: 200, height: 'auto', border: 'none !important'}}>
          {image ? (
            <>
              <div className="crop-container" style={{ position: 'relative', width: '200px', height: '200px' }}>
                <Cropper
                  image={image}
                  crop={crop}
                  zoom={zoom}
                  aspect={1} // Maintain square aspect ratio
                  cropShape="round" // Crop as a circle
                  onCropChange={setCrop}
                  onZoomChange={setZoom}
                  onCropComplete={onCropComplete}
                />
              </div>

              {/* <Button variant="contained" onClick={handleCrop} sx={{ marginTop: 1 }}>
                Crop Image
              </Button> */}

            </>
          ) : (
            <ImageButton
              url={process.env.PUBLIC_URL + "/selectInput.jpg"}
              title="Stencil"
              size={200}
              onFileChange={handleFileChange}
            />
          )}
        </Card>
      </AccordionDetails>
    </Accordion>
  );
});

export default InputSelection;
