import React, { useEffect, useState, useCallback } from "react";
import { useLocation } from 'react-router-dom';
import "./find.css";
import { Box, Button, CircularProgress, Grid } from "@mui/material";
import { Download as DownloadIcon, Launch as LaunchIcon, Delete as DeleteIcon, PlayArrow as PlayIcon } from "@mui/icons-material";
import StakcoAppBar from "./../../components/StakcoAppBar";

import { getSimulationURI } from '../../utils/resourceLinker';

const stakco_backend_url = "https://stakco.uc.r.appspot.com/api/stakco";
// const stakco_backend_url = "https://localhost:52729/api/stakco";

const stakco_images_url = "https://storage.googleapis.com/stakco-images";

function Find() {
  const location = useLocation();
  const storedUsername = localStorage.getItem('username');
  const userPermission = localStorage.getItem('userpermission');
  const { username } = location.state || { username: storedUsername };
  const { userpermission } = location.state || { userpermission: userPermission };
  console.log("permission level: " + userpermission);

  const [projects, setProjects] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  const [deleteStates, setDeleteStates] = useState({}); // Track delete button states

  const [deleting, setDeleting] = useState({}); // Track which projects are being deleted

  const removeProjectFromList = (folderName) => {
    setProjects((prevProjects) => prevProjects.filter((project) => project !== folderName));
  };
  

  const handleIntermediateDelete = (folderName) => {
    setDeleteStates((prev) => ({
      ...prev,
      [folderName]: "Press again to delete",
    }));

    // Reset button text back to "Delete" after 2 seconds if not clicked again
    setTimeout(() => {
      setDeleteStates((prev) => {
        if (prev[folderName] === "Press again to delete") {
          return { ...prev, [folderName]: "Delete" };
        }
        return prev;
      });
    }, 2000);
  };

  const confirmDelete = async (folderName) => {
    if (deleteStates[folderName] === "Press again to delete") {
      setDeleting((prev) => ({ ...prev, [folderName]: true })); // Disable the block
      await handleDeleteProject(folderName); // Call delete function
      setDeleting((prev) => ({ ...prev, [folderName]: false })); // Reset if needed
    } else {
      handleIntermediateDelete(folderName);
    }
  };

  const fetchProjects = useCallback(async () => {
    if (!username) {
      console.error("Username is not provided");
      return;
    }
    setIsLoading(true);
    try {
      const response = await fetch(`${stakco_backend_url}/${username}`);
      const data = await response.json();
      setProjects(data);
    } catch (error) {
      console.error("Error fetching projects:", error);
    } finally {
      setIsLoading(false);
    }
  }, [username]);

  useEffect(() => {
    fetchProjects();
  }, [fetchProjects]);

  const handleDownloadLayer = useCallback((folderName) => {
    const noOfLayers = parseInt(folderName.split('_')[1], 10); // Get the number of layers

    for (let i = 0; i < noOfLayers; i++) {
      const imageUrl = `${stakco_images_url}/${username}/${folderName}/${folderName.split('_')[0]}_${i}.png`;
      console.log(imageUrl);

      fetch(imageUrl, { method: 'GET', mode: 'cors' })
        .then((response) => {
          if (!response.ok) {
            throw new Error(`Failed to fetch image for layer ${i}`);
          }
          return response.blob();
        })
        .then((blob) => {
          const a = document.createElement('a');
          a.href = URL.createObjectURL(blob);
          a.download = `${folderName}_${i}.png`;
          document.body.appendChild(a);
          a.click();
          document.body.removeChild(a);
        })
        .catch((error) => console.error(`Error downloading layer ${i}:`, error));
    }
  }, [username]);

  const handleDeleteProject = useCallback(async (folderName) => {
    console.log("Deleting folder:", folderName);

    try {
        setDeleteStates((prev) => ({
          ...prev,
          [folderName]: "Deleting",
        }));
        const response = await fetch(`${stakco_backend_url}/${username}/${folderName}`, {
            method: "DELETE",
            headers: {
                "Content-Type": "application/json",
            },
        });

        if (!response.ok) {
            // Handle non-200 responses
            const errorData = await response.json();
            console.error("Error deleting project:", errorData.message);
            alert(`Failed to delete project: ${errorData.message}`);
        } else {
            const result = await response.json();
            console.log("Delete response:", result);
            if (result.success) {
                removeProjectFromList(folderName); // Remove project from the list
            } else {
                setDeleteStates((prev) => ({
                  ...prev,
                  [folderName]: "Delete",
                }));
                alert("Failed to delete project. Please try again.");
            }
        }
    } catch (error) {
        setDeleteStates((prev) => ({
          ...prev,
          [folderName]: "Delete",
        }));
        console.error("Error deleting project:", error);
        alert("An error occurred while deleting the project. Please try again.");
    } finally {
    }
}, [username]);

  const handleOpenProject = useCallback((folderName) => {
    let link = getSimulationURI(folderName);
    window.open(link+"_image", '_blank', 'noopener,noreferrer');
  }, []);

  const handleSendPlayer = useCallback((folderName)=>{
    // Copy the link to the clipboard
    let link = getSimulationURI(folderName);

    navigator.clipboard.writeText(link)
        .then(() => {
            console.log('Link copied to clipboard!');
        })
        .catch((err) => {
            console.error('Failed to copy the link: ', err);
        });
  }, [])

  return (
    <div className="stakco-app">
      <StakcoAppBar />

      <Box sx={{ margin: "100px 30px 30px 30px" }}>
        {isLoading ? (
          <CircularProgress />
        ) : (
          <Grid container spacing={2}>
            {projects.map((project, index) => (
              <Grid item xs={12} sm={6} md={2} key={index}
              className={deleting[project] ? "is-deleting" : ""}
                style={{
                  position: "relative",
                  opacity: deleting[project] ? 0.5 : 1,
                  pointerEvents: deleting[project] ? "none" : "auto",
                }}
                >
                <div className="project-card">
                  <img
                    src={`${stakco_images_url}/${username}/${project}/${project.split('_')[0]}_1.png`}
                    alt={`Project ${index + 1}`}
                    className="project-image"
                  />
                  <div className="project-actions">
                    <Button
                      variant="contained"
                      color="primary"
                      startIcon={<DownloadIcon />}
                      onClick={() => handleDownloadLayer(project)}
                      sx={{ marginTop: "10px", backgroundColor: "darkorange", "&:hover": { backgroundColor: "darkred" } }}
                      fullWidth
                    >
                      Download
                    </Button>
                    <Button
                      variant="contained"
                      color="primary"
                      startIcon={<LaunchIcon />}
                      onClick={() => handleOpenProject(project)}
                      sx={{ marginTop: "10px", backgroundColor: "darkorange", "&:hover": { backgroundColor: "darkred" } }}
                      fullWidth
                    >
                      Open
                    </Button>
                    <Button
                      variant="contained"
                      color="primary"
                      startIcon={<PlayIcon />}
                      onClick={() => handleSendPlayer(project)}
                      sx={{ marginTop: "10px", backgroundColor: "darkorange", "&:hover": { backgroundColor: "darkred" } }}
                      fullWidth
                    >
                      Send to Player
                    </Button>
                    <Button
                      variant="contained"
                      color="primary"
                      startIcon={<DeleteIcon />}
                      onClick={() => confirmDelete(project)}
                      sx={{ marginTop: "10px", backgroundColor: "darkorange", "&:hover": { backgroundColor: "darkred" } }}
                      fullWidth
                    >
                      {deleteStates[project] || "Delete"}
                    </Button>
                  </div>
                </div>
              </Grid>
            ))}
          </Grid>
        )}
      </Box>
    </div>
  );
}

export default Find;
